export default {
	data() {
		return {
			// modal默认不显示
			modal:null,
			// 加载显示内容
			title:"审核不通过",
			// 提交内容
			content: "",
			//
			viewText:"",
			// 初始空
			text:"",
			// 默认不显示textarea
			showElem:false,
			showFocus:false
		}
	},
	methods: {
		show(data){
			if (typeof data === 'object' && data) {
				this.title = data.title;
			}
			this.text = "";
			this.showFocus = false;
			this.viewText = "";
			this.modal = "show";
		},
		// 隐藏
		hide(){
			this.showFocus = false;
			this.modal = null;
			this.showElem = false;
		},
		// 确定
		submit(){
			// 触发父页面v-on:pouple方法
			let content = this.content;
			this.$emit("pouple",content);
		},
		// 监听输入
		monitorInput(e){
			this.content = e.detail.value;
			this.viewText = e.detail.value;
		},
		//点击view
		clickView(){
			this.showFocus = true;
			this.showElem = true;
		}
		
	}
}
